import styled from 'styled-components';
import {FiArrowRight,FiCircle} from "react-icons/fi";


export const FrontContainer =styled.div`
background: #222629;
display:flex;
justify-content:center;
align-items:center;
padding:0 30px;
height :800px;
position :relative;
z-index:1;

.before{
    content:'';
    position:absolute;
    top:0;
    bottom:0;
    left:0;
    right:0;
    background: linear-gradient(180deg, rgba(0,0,0,0.2) 0% ,rgba(0,0,0,0.6)100%),
    linear-gradient(180deg, rgba(0,0,0,0.2) 0% ,transparent 100%);
    z-index:2;
}

`
;

export const FrontBg=styled.div`
position:absolute;
top:0;
right:0;
bottom:0;
left:0;
width:100%;
height:100%;
overflow:hidden;


`;


export const VideoBg =styled.video`
width:100%;
height:100%;
-o-object-fit:cover;
object-fit:cover;
background:#232a34;
`;

export const FrontContent=styled.div`
z-index:3;
max-width:1200px;
positions:absolute;
padding:8px 24px;
display:flex;
flex-direction:column;
align-items:center;
`;

export const FrontBanner=styled.div`
display:inline-block;
vertical-align:middle;
margin:0 auto;
padding-bottom:50px;
padding-top:50px;
text-align:center;
background-color:rgba(0,0,0,0.6);
border-radius:20px;

`
;

export const Fronth1=styled.h1`
color: #fff;
font-size:48px;
text-align:center;

@media screen and (max-width:768px){
font-size:40px;
}
@media screen and (max-width:480px){
font-size:32px;


}
`;

export const Frontp=styled.p`
margin-top:24px;
color:#fff;
font-size:24px;
text-align:center;
max-width:600px;

@media screen and (max-width:768px){
    font-size:24px;
}
@media screen and (max-width:480px){
    font-size:18px;    
}
`;

export const FrontBtnWrapper = styled.div`
margin-top:32px;
display:flex;
flex-directions:column;
align-items:center;

`;


export const ArrowBottom =styled(FiArrowRight)`
margin-left:8px;
font-size:20px;

`;

export const ArrowCircle=styled(FiCircle)`
margin-left:8px;
font-size:20px;
`;